<template>
  <div>
     <van-search shape="round" placeholder="请输入产品名称...."></van-search>
     <van-grid>
        <van-grid-item :text="$route.query.title" />
        <van-grid-item :text="condition.priceOrder=='desc'?'价格降序':'价格升序'" @click="changePrice"/>
        <van-grid-item text="销量" />
        <van-grid-item text="新品" />
      </van-grid>
      <van-grid column-num="2">
        <van-grid-item v-for="item in productList" :key="item.id">
          <router-link :to="`/detail/${item.id}`">
            <van-image width="100%" :src="item.image"/>
          </router-link>
          <span class="sp-text">{{item.store_name}}</span>
          <p class="p-text">￥{{item.price}}</p> 
          <div>
             <span class="span-price price">￥:{{item.vip_price}}</span>
             <van-image class="vip-img" :src="require('@/assets/login/vip.png')"/>
          <span class="span-price sales">已售:{{item.sales}}</span>
          </div>

        </van-grid-item>
      </van-grid>
      <div class="null"></div>
  </div>
</template>
<script>
export default {
  name:"GoodList",
  props:["id"],
  data(){
    return {
      productList:[],
      condition:{
          page:1,
          limit:10,
          keyword:'',
          sid:0,
          news:0,//如果需要展示新品1
          priceOrder:'',//asc升序 desc降序
          salesOrder:'',//asc升序 desc降序
      }
    }
  },
  created(){
    this.getProduct();
  },
  methods:{
    changePrice(){
      if(this.condition.priceOrder=='desc'){
         this.condition.priceOrder='asc';
      }else{
        this.condition.priceOrder='desc';
      }
      this.getProduct();
    },
    getProduct(){
      this.condition.sid=this.id;
      this.$axios.get('/api/products',{
        params:this.condition
      }).then(d=>{
        if(d.data.status==200) this.productList=d.data.data;
        console.log('商品列表',d)
      });
    }
  }
}
</script>
<style lang="less" scoped>
.sp-text{
  margin-top: 10px;
  display: block;
  width: 150px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  
}
.p-text{
  // padding-top: 6px;
  font-size: 16px;
  color: #333;
  float: left;
  margin-top: 8px;
  // display: block;
  width: 150px;
  color: red;
  margin-bottom: 2px;
  
}
.vip-img{
  width: 25px;
}
.span-price{
  display: inline-block;
  margin:5px;
 

}
.price{
  font-size: 10px;
 
}
.sales{
  margin-left: 20px;
  font-size: 10px;
}
.null{
  height: 50px;
}
</style>